import styled from "styled-components";

export const FooterContainer = styled.div`
    display: flex;
    flex-direction: row;
    /* grid-template-columns: 5fr 3fr ; */
    flex-wrap: wrap;
    background: #081728;
    color: #fff;
    list-style: none;
    padding-top: 50px;
    padding-bottom: 30px;
    padding-left: 6%;
    justify-content: space-around;

    @media screen and (max-width:680px) {
        justify-content: flex-start;
        flex-direction: column;
    }
`

export const Copyright = styled.div`
    display: flex;
    justify-content: center;
    background: #081728;
    color: #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: .8rem;
`

export const Links = styled.div`
    display: flex;
    flex-flow: column;
    width: 30%;
    line-height: 1.4;
    text-align: left;
    
    @media screen and (max-width:960px) {
        width: 100%;
        max-width: 100%;
        align-items: flex-start;
        padding-left: 8%;
        padding-right: 12%;
        margin-bottom: 35px;
    }
`

export const Socials = styled.div`
    display: flex;
    background: #081728;
    list-style: none;
    justify-content: center;
    align-items: center;
`

export const IHolder = styled.a`
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(200,200,200,0.5);
`

export const CLink = styled.a`
    color: #fff;
    text-decoration-line: none;
    &:hover {
        color: antiquewhite;
        text-decoration-line: underline;
    }
`