import styled from "styled-components";
import RegisterSVG from '../../assets/registerSvg.svg';

export const RegisterHeading = styled.h2`
    display: flex;
    padding-left: 6%;
    font-size: 1.8rem;
    color: #001345;
    background: #F9EEEE;
    padding-top: 50px;
    padding-bottom: 20px;
    font-family: 'Poppins';
`

export const RegisterContainer = styled.div`
    display: flex;
    flex-wrap: wrap;
    padding-left : 6%;
    padding-top: 2%;
    padding-bottom: 8%;
    list-style: none;
    background: #F9EEEE;
    background-image: url(${RegisterSVG});
    background-repeat: no-repeat;
    background-size: 45%;
    background-position-x: 60%;
    background-position-y: 50%;
    @media screen and (max-width: 760px){
        column-count: 1;
        background: #F9EEEE;
    }

    @media screen and (max-width: 960px) {
        background-position-x: 90%;
        background-position-y: 0%;
    }
`

export const TextContainer = styled.div`
    width: 45%;
    margin-bottom: 50px;
    color: #001345;
    @media screen and (max-width : 960px){
        width: 500px;
    }
`

export const FormContainer = styled.div`
    display: flex;
    width: 50%;
    padding-top: 1%;
    padding-left: 5%;
    margin-bottom: 5%;
    @media screen and (max-width : 960px){
        width: 100%;
        padding-left: 0%;
    }

`

export const RHead = styled.h3`
    display: flex;
    height: 50px;
    align-items: center;
    margin-top: 15px;
    margin-bottom: 6x;
`

export const Clist = styled.li`
    line-height: 1.5;
    font-size: .95rem;
    color: #390011;
    border-left: 5px solid #002050;
    padding-left: 8px;
`

export const CForm = styled.form`
    display: flex;
    flex-flow: column;
    align-items: center;
    background: rgba(255,255,255,0.5); 
    backdrop-filter: blur(4px);
    padding-top: 20px;
    padding-left:  35px;
    padding-right: 35px;
    padding-bottom: 35px;
    border-radius: 10px;
    box-shadow: 0px 0px 8px rgba(0,0,0,0.1);
    @media screen and (max-width : 960px){
        width: 100%;
        padding-left: 6%;
        padding-right: 6%;
        margin-right: 6%;
    }
`

export const CFIeld = styled.input`
    height: 50px;
    width: 350px;
    padding-left: 15px;
    margin-top: 15px;
    margin-bottom: 15px;
    outline: none;
    transition: 0.25s;
    border-radius: 6px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    &:focus {
        border: 2px solid rgba(255,215,215,1);
    }

    ::-webkit-inner-spin-button{
        -webkit-appearance: none; 
        margin: 0; 
    }
    ::-webkit-outer-spin-button{
        -webkit-appearance: none; 
        margin: 0; 
    }

    @media screen and (max-width : 960px){
        width: 100%;
    }
`

export const Button = styled.div`
    background-color: rgba(205,135,135,0.8);
    width: 350px;
    padding: 14px;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    cursor: pointer;
    font-family: Poppins;
    transition: 0.25s;
    border: 1px solid rgba(205,135,135,1);
    box-shadow: 0px 0px 8px 1px rgba(95,95,95,0.5);

    &:hover {
        background-color: rgba(205,135,135,1);
    }

    @media screen and (max-width : 960px){
        width: 100%;
    }
`