import React from 'react'
import { CInfoContainer } from './ContactFragElement'


export default function Card({name,department,email,phone}) {
    return (
        <CInfoContainer>
            <h3 style={{'fontFamily':'Poppins','fontSize':'1.3rem'}}><br/>Convenor</h3><br/>
            <p style={{'fontWeight':'bolder'}}>{name}</p>
            <p>{department}</p>
            <p>Dr. Sudhir Chandra Sur Engineering College and Sports Complex</p>
            <p>Email : {email}</p>
            <p>Phone : {phone}</p>
        </CInfoContainer>
    )
}