import React from 'react'
import { FooterContainer, Copyright, Links, Socials, IHolder, CLink } from './FooterElements'
import {FaFacebook, FaLinkedin, FaYoutube} from 'react-icons/fa'
// import {Link as LinkR} from 'react-router-dom';

const Footer = () => {
    const paraS = {
        'paddingRight': '12%',
        'lineHeight': '25px',
        'marginBottom': '25px',
        'display':'flex',
        'justifyContents':'flex-start'}

    const listS2 = {'lineHeight':'30px'}
    
    const linkProp = {
        borderLeft:"4px solid #fff",
        paddingLeft:"12px",
        lineHeight:"1.8"
        }

    const headProp = {
        fontFamily:"Poppins",
        fontSize:"1.3rem",
        marginBottom:"25px"
    }

    return (
        <>
            <FooterContainer>
                <Links>
                    <h3 style={headProp}>ABOUT DSCSDEC</h3>
                    <p style={paraS}>Dr. Sudhir Chandra Sur Institute of Technology {'&'} Sports Complex, located in Kolkata. The college is one of the top among the 30 institutions in West Bengal owned by JIS Group, the largest educational conglomerate in Eastern India.</p>
                </Links>
                
                <Links>
                    <h3 style={headProp}>QUICK LINKS</h3>
                    <div style={linkProp}>
                        <li><CLink href='https://www.jisgroup.org/' target='_blank'>JIS Group</CLink></li>
                        <li><CLink href='https://www.surtech.edu.in/' target='_blank'>DSCSDEC</CLink></li>
                        <li><CLink>FAQs</CLink> </li>
                    </div>
                </Links>
                
                <Links>
                    <h3 style={headProp}>IMPORTANT LINKS</h3>
                    <div style={linkProp}>
                        
                        <li><CLink href='' target='_blank'>Download Brochure</CLink></li>
                        <li><CLink href='' target='_blank'>Call for Papers</CLink></li>
                        <li><CLink href='' target='_blank'>Paper Format</CLink></li>
                        <li><CLink href='' target='_blank'>Committee</CLink></li>
                        
                        
                    </div>
                </Links>
            </FooterContainer>
            
            <Socials style={listS2}> 
                <IHolder target="_blank" href="https://www.youtube.com/channel/UCXtDx43W4KtIlSIjbrXMx-A" rel="noreferrer">
                    <FaYoutube color='fff'/></IHolder> &nbsp;&nbsp;
                <IHolder target="_blank" href="https://www.facebook.com/SurTechCollege/" rel="noreferrer">
                    <FaFacebook color='fff'/></IHolder> &nbsp;&nbsp;
                <IHolder target="_blank" href="https://www.linkedin.com/company/dr-sudhir-chandra-sur-institute-of-technology-sports-complex/" rel="noreferrer">
                    <FaLinkedin color='fff'/></IHolder>
            </Socials>
            <Copyright >SURTECH Â© 2022 All Rights Reserved</Copyright>
        </>
        
    )
}

export default Footer